$black: #000000;
$black-a: #131D24;
$white: #FFFFFF;
$rgbaWhite: rgba(255, 255, 255, 1);
$white-a: #F7F9FA;
$rgbaWhite-a: rgba(247, 249, 250, 1);
$borderColor: #D8D8D8;
$grey: #626262;
$rgbaGrey: rgba(98, 98, 98, 1);
$rgbaGrey-two: rgba(255, 255, 255, .55);
$rgbaGrey-three: rgba(177, 177, 177, 1);
$rgbaBgModal: rgba(229, 229, 229, 1);
$red: #EF4036;
$redGradient: linear-gradie(180deg, #EF4036 0%, #9F2019 100%);
$whiteGradient: linear-gradient(180deg, #EF4036 0%, #9F2019 100%);
$bgWhite: rgba(216, 216, 216, 1);
$bgRed: rgba(239, 64, 54, 1);


//   
$minWidth: 320;
//   ()
$maxWidth: 1920;
//    (0 =  )
$maxWidthContainer: 1620;
//    
// (30 =  15px   , 0 =  )
$containerPadding: 40;

//    
$containerWidth: $maxWidthContainer + $containerPadding;
:root {
	--containerWidth: 1620;
}

// -
$pc: em($containerWidth); // , ,     
$tablet: em(991.98); // ,     
$mobile: em(767.98); //  L
$mobileSmall: em(479.98); //  S

//  :
// 1 =  (   ),
// 2 =  - (     -)
$responsiveType: 1;
//   
$font-family-sans-serif: system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", "Noto Sans", "Liberation Sans", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji" !default;
$font-family-monospace: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace !default;
$fontFamily: 'Geometria', $font-family-sans-serif, $font-family-monospace;
$fontSize: rem(15); //  14(px) -      
$fontWeight: 500;
$lineHeight: 1;
:root {
	--minFont: .9375; //   -   min  rem
	--maxFont: .9375; //   -   max  rem
}

//  
$mainColor: $black; //    

$bgHeader: ('color': $mainColor);

$variables: (
		'Colors': (
				'color': (
						'primary': #000,
						'success': #27BA6C,
						'info': #03a9f4,
						'warning': #FF8833,
						'danger': #ff1a1a
				)
		),
		'Background': (
				'color': (
						'black': #000000,
						'white': #FFFFFF,
						'black-rgba': rgba(0, 0, 0, .55),
						'white-rgba': rgba(255, 255, 255, .55),
						'main-bg': #E5E5E5
				)
		),
		'Indents': (
				'size': (
						'top': 20px,
						'right': 20px,
						'bottom': 20px,
						'left': 20px,
				)
		)
);
//padding-top: map_get($variables, 'Indents', 'size', 'top');


$status-colors: (
		primary: #000,
		success: #27BA6C,
		info: #03a9f4,
		warning: #FF8833,
		danger: #ff1a1a
);
